import { updateFontStyles } from "../lib/font-styles";

function awaitFonts(callback, duration) {
	updateFontStyles(); // Gets fonts to start loading

	callback = callback || window.template.update;

	// Start counting the specified max delay
	var timeout = setTimeout(callback, isNaN(duration) ? 500 : duration);

	// The event listener won't work on browsers that don't support the CSS Font
	// Loading Module API, so we just return and let the callback fire via the timeout.
	// This way the fonts have a chance of loading pre-render.
	if (!document.fonts) return;

	// Otherwise call the callback when a new font has loaded, if this happens before
	// the timeout fires
	document.fonts.ready
		.then(function(e) {
			if (e.size) {
				clearTimeout(timeout);
				callback();
			}
			document.fonts.addEventListener("loadingdone", function() {
				clearTimeout(timeout);
				callback();
			});
		})
		.catch(function (err) {
			console.log("Error loading fonts", err);
		});
}

export { awaitFonts };
